import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MeetingRoomReservationPageComponent } from './components/meeting-room-reservation-page/meeting-room-reservation-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';

const routes: Routes = [
  {
    path: '',
    component: MeetingRoomReservationPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Бронирования переговорных' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [ GroupsResolver ],
    exports: [RouterModule]
})
export class BookingRoutingModule { }
